const statusConfig = {
  todo: { label: "To Do", icon: Circle, color: "text-muted-foreground" },
  "in-progress": { label: "In Progress", icon: Clock, color: "text-primary" },
  review: { label: "Review", icon: AlertTriangle, color: "text-warning" },
  completed: { label: "Completed", icon: CheckSquare, color: "text-success" },
}

const priorityConfig = {
  low: { label: "Low", color: "bg-muted text-muted-foreground", flagColor: "text-muted-foreground" },
  medium: { label: "Medium", color: "bg-chart-1/20 text-chart-1", flagColor: "text-chart-1" },
  high: { label: "High", color: "bg-warning/20 text-warning", flagColor: "text-warning" },
  urgent: { label: "Urgent", color: "bg-destructive/20 text-destructive", flagColor: "text-destructive" },
}

export default function TasksPage() {
  const [tasks, setTasks] = useState<Task[]>(initialTasks)
  const [searchQuery, setSearchQuery] = useState("")
  const [view, setView] = useState<"board" | "list" | "calendar" | "timeline" | "grid">("board")
  const [statusFilter, setStatusFilter] = useState<string>("all")
  const [priorityFilter, setPriorityFilter] = useState<string>("all")
  const [isAddDialogOpen, setIsAddDialogOpen] = useState(false)
  const [expandedTasks, setExpandedTasks] = useState<string[]>([])
  const [selectedTask, setSelectedTask] = useState<Task | null>(null)
  const [isTaskDetailsOpen, setIsTaskDetailsOpen] = useState(false)
  const [activeTimer, setActiveTimer] = useState<{ taskId: string; startTime: number } | null>(null)

  const filteredTasks = tasks.filter((task) => {
    const matchesSearch =
      task.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
      task.project.toLowerCase().includes(searchQuery.toLowerCase())
    const matchesStatus = statusFilter === "all" || task.status === statusFilter
    const matchesPriority = priorityFilter === "all" || task.priority === priorityFilter
    return matchesSearch && matchesStatus && matchesPriority
  })

  const stats = {
    total: tasks.length,
    todo: tasks.filter((t) => t.status === "todo").length,
    inProgress: tasks.filter((t) => t.status === "in-progress").length,
    review: tasks.filter((t) => t.status === "review").length,
    completed: tasks.filter((t) => t.status === "completed").length,
  }

  const toggleTaskExpansion = (taskId: string) => {
    setExpandedTasks((prev) => (prev.includes(taskId) ? prev.filter((id) => id !== taskId) : [...prev, taskId]))
  }

  const toggleTaskStatus = (taskId: string) => {
    setTasks((prev) =>
      prev.map((task) =>
        task.id === taskId ? { ...task, status: task.status === "completed" ? "todo" : "completed" } : task,
      ),
    )
  }

  const toggleSubtask = (taskId: string, subtaskId: string) => {
    setTasks((prev) =>
      prev.map((task) =>
        task.id === taskId
          ? {
            ...task,
            subtasks: task.subtasks?.map((st) => (st.id === subtaskId ? { ...st, completed: !st.completed } : st)),
          }
          : task,
      ),
    )
  }

  const handleAddTask = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault()
    const formData = new FormData(e.currentTarget)
    const newTask: Task = {
      id: Date.now().toString(),
      title: formData.get("title") as string,
      description: formData.get("description") as string,
      status: "todo",
      priority: formData.get("priority") as "low" | "medium" | "high" | "urgent",
      project: formData.get("project") as string,
      projectCategory: "Development", // Default for now
      assignee: { name: "John Doe", initials: "JD" },
      dueDate: formData.get("dueDate") as string,
      tags: (formData.get("tags") as string).split(",").map((t) => t.trim()),
      subtasks: [],
      comments: [],
      attachments: [],
      timeLogs: [],
      estimatedTime: Number(formData.get("estimatedTime")) || 0,
      spentTime: 0,
      budget: Number(formData.get("budget")) || 0,
    }
    setTasks([newTask, ...tasks])
    setIsAddDialogOpen(false)
  }

  const handleDeleteTask = (taskId: string) => {
    setTasks((prev) => prev.filter((t) => t.id !== taskId))
  }

  // Group tasks by status for kanban view
  const groupedTasks = {
    todo: filteredTasks.filter((t) => t.status === "todo"),
    "in-progress": filteredTasks.filter((t) => t.status === "in-progress"),
    review: filteredTasks.filter((t) => t.status === "review"),
    completed: filteredTasks.filter((t) => t.status === "completed"),
  }
  const formatDate = (dateString: string | null) => {
    if (!dateString) return "N/A"
    return new Date(dateString).toLocaleDateString("en-US", { month: "short", day: "numeric" })
  }

  return (
    <DashboardLayout>
      <div className="space-y-6">
        {/* Page Header */}
        <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-4 animate-in fade-in slide-in-from-top-4 duration-500">
          <div>
            <h1 className="text-2xl font-bold text-foreground">Tasks</h1>
            <p className="text-muted-foreground mt-1">Manage and track all your tasks across projects</p>
          </div>
          <Dialog open={isAddDialogOpen} onOpenChange={setIsAddDialogOpen}>
            <DialogTrigger asChild>
              <Button className="gap-2">
                <Plus className="w-4 h-4" />
                Add Task
              </Button>
            </DialogTrigger>
            <DialogContent className="sm:max-w-[500px]">
              <DialogHeader>
                <DialogTitle>Create New Task</DialogTitle>
              </DialogHeader>
              <form onSubmit={handleAddTask} className="space-y-4">
                <div className="space-y-2">
                  <Label htmlFor="title">Task Title</Label>
                  <Input id="title" name="title" placeholder="Design homepage wireframes" required />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="description">Description</Label>
                  <Textarea id="description" name="description" placeholder="Task details..." />
                </div>
                <div className="grid grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="project">Project</Label>
                    <Select name="project" defaultValue="E-commerce Redesign">
                      <SelectTrigger>
                        <SelectValue placeholder="Select project" />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="E-commerce Redesign">E-commerce Redesign</SelectItem>
                        <SelectItem value="SEO Campaign Q4">SEO Campaign Q4</SelectItem>
                        <SelectItem value="Social Media Strategy">Social Media Strategy</SelectItem>
                        <SelectItem value="PPC Management">PPC Management</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="priority">Priority</Label>
                    <Select name="priority" defaultValue="medium">
                      <SelectTrigger>
                        <SelectValue placeholder="Select priority" />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="low">Low</SelectItem>
                        <SelectItem value="medium">Medium</SelectItem>
                        <SelectItem value="high">High</SelectItem>
                        <SelectItem value="urgent">Urgent</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>
                </div>
                <div className="grid grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="dueDate">Due Date</Label>
                    <Input id="dueDate" name="dueDate" type="date" required />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="tags">Tags (comma separated)</Label>
                    <Input id="tags" name="tags" placeholder="Design, UI/UX" />
                  </div>
                </div>
                <DialogFooter>
                  <Button type="button" variant="outline" onClick={() => setIsAddDialogOpen(false)}>
                    Cancel
                  </Button>
                  <Button type="submit">Create Task</Button>
                </DialogFooter>
              </form>
            </DialogContent>
          </Dialog>
        </div>

        {/* Stats */}
        <div className="grid grid-cols-2 md:grid-cols-5 gap-4">
          {[
            { label: "Total Tasks", value: stats.total, icon: CheckSquare, color: "text-foreground" },
            { label: "To Do", value: stats.todo, icon: Circle, color: "text-muted-foreground" },
            { label: "In Progress", value: stats.inProgress, icon: Clock, color: "text-primary" },
            { label: "In Review", value: stats.review, icon: AlertTriangle, color: "text-warning" },
            { label: "Completed", value: stats.completed, icon: CheckSquare, color: "text-success" },
          ].map((stat, i) => (
            <AnimatedCard key={stat.label} delay={i * 50} className="p-4">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-xs text-muted-foreground">{stat.label}</p>
                  <p className={`text-xl font-bold mt-1 ${stat.color}`}>{stat.value}</p>
                </div>
                <stat.icon className={`w-8 h-8 ${stat.color} opacity-20`} />
              </div>
            </AnimatedCard>
          ))}
        </div>

        {/* Filters and View Switcher */}
        <AnimatedCard delay={250} className="p-4">
          <div className="flex flex-col lg:flex-row gap-4 justify-between">
            <div className="flex flex-1 flex-col sm:flex-row gap-4">
              <div className="relative flex-1">
                <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-muted-foreground" />
                <Input
                  placeholder="Search tasks..."
                  className="pl-10 bg-secondary border-0"
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                />
              </div>
              <div className="flex gap-2">
                <Select value={statusFilter} onValueChange={setStatusFilter}>
                  <SelectTrigger className="w-[130px] bg-secondary border-0 text-xs">
                    <SelectValue placeholder="Status" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">All Status</SelectItem>
                    <SelectItem value="todo">To Do</SelectItem>
                    <SelectItem value="in-progress">In Progress</SelectItem>
                    <SelectItem value="review">Review</SelectItem>
                    <SelectItem value="completed">Completed</SelectItem>
                  </SelectContent>
                </Select>
                <Select value={priorityFilter} onValueChange={setPriorityFilter}>
                  <SelectTrigger className="w-[130px] bg-secondary border-0 text-xs">
                    <SelectValue placeholder="Priority" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">All Priority</SelectItem>
                    <SelectItem value="urgent">Urgent</SelectItem>
                    <SelectItem value="high">High</SelectItem>
                    <SelectItem value="medium">Medium</SelectItem>
                    <SelectItem value="low">Low</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </div>

            <div className="flex items-center gap-1 bg-secondary/30 p-1 rounded-lg self-start">
              {[
                { id: "board", label: "Board", icon: Trello },
                { id: "list", label: "List", icon: ListIcon },
                { id: "calendar", label: "Calendar", icon: CalendarIcon },
                { id: "timeline", label: "Timeline", icon: History },
                { id: "grid", label: "Grid", icon: Layout },
              ].map((v) => (
                <Button
                  key={v.id}
                  variant={view === v.id ? "secondary" : "ghost"}
                  size="sm"
                  className={cn("h-8 px-3 gap-2 text-xs", view === v.id && "bg-background shadow-sm")}
                  onClick={() => setView(v.id as any)}
                >
                  <v.icon className="w-3.5 h-3.5" />
                  <span className="hidden sm:inline">{v.label}</span>
                </Button>
              ))}
            </div>
          </div>
        </AnimatedCard>

        {/* Task Views */}
        <div className="min-h-[60vh]">
          {view === "board" && (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 animate-in fade-in duration-500">
              {(Object.keys(groupedTasks) as Array<keyof typeof groupedTasks>).map((status, colIndex) => {
                const StatusIcon = statusConfig[status].icon
                return (
                  <div key={status} className="flex flex-col gap-4">
                    <div className="flex items-center justify-between px-2 py-1">
                      <div className={cn("flex items-center gap-2 font-semibold text-sm", statusConfig[status].color)}>
                        <StatusIcon className="w-4 h-4" />
                        {statusConfig[status].label}
                      </div>
                      <Badge variant="secondary" className="text-[10px] h-5">
                        {groupedTasks[status].length}
                      </Badge>
                    </div>
                    <div className="flex flex-col gap-3">
                      {groupedTasks[status].map((task, i) => (
                        <AnimatedCard
                          key={task.id}
                          delay={i * 50}
                          className={cn(
                            "p-3 cursor-pointer group hover:ring-2 hover:ring-primary/20",
                            task.status === "completed" && "opacity-75",
                          )}
                          onClick={() => {
                            setSelectedTask(task)
                            setIsTaskDetailsOpen(true)
                          }}
                        >
                          <div className="space-y-3">
                            <div className="flex items-start justify-between gap-2">
                              <h4
                                className={cn(
                                  "font-medium text-sm leading-tight flex-1",
                                  task.status === "completed" && "line-through text-muted-foreground",
                                )}
                              >
                                {task.title}
                              </h4>
                              <DropdownMenu>
                                <DropdownMenuTrigger asChild onClick={(e) => e.stopPropagation()}>
                                  <Button variant="ghost" size="icon" className="h-6 w-6 opacity-0 group-hover:opacity-100">
                                    <MoreHorizontal className="h-4 w-4" />
                                  </Button>
                                </DropdownMenuTrigger>
                                <DropdownMenuContent align="end">
                                  <DropdownMenuItem>
                                    <Edit className="w-4 h-4 mr-2" /> Edit
                                  </DropdownMenuItem>
                                  <DropdownMenuItem className="text-destructive">
                                    <Trash2 className="w-4 h-4 mr-2" /> Delete
                                  </DropdownMenuItem>
                                </DropdownMenuContent>
                              </DropdownMenu>
                            </div>

                            <div className="flex flex-wrap gap-1">
                              <Badge className={cn("text-[10px] px-1.5 py-0 h-4 border-0", priorityConfig[task.priority].color)}>
                                {priorityConfig[task.priority].label}
                              </Badge>
                              {task.tags.map((tag) => (
                                <Badge key={tag} variant="outline" className="text-[10px] px-1.5 py-0 h-4 font-normal">
                                  {tag}
                                </Badge>
                              ))}
                            </div>

                            <div className="flex items-center justify-between pt-2 border-t border-border/50">
                              <div className="flex -space-x-1.5 overflow-hidden">
                                <Avatar className="w-5 h-5 border-2 border-background">
                                  <AvatarFallback className="bg-primary/20 text-primary text-[8px]">
                                    {task.assignee.initials}
                                  </AvatarFallback>
                                </Avatar>
                              </div>
                              <div className="flex items-center gap-2 text-[10px] text-muted-foreground">
                                <span className="flex items-center gap-1">
                                  <CalendarIcon className="w-3 h-3" />
                                  {formatDate(task.dueDate)}
                                </span>
                                {task.subtasks.length > 0 && (
                                  <span className="flex items-center gap-1">
                                    <CheckSquare className="w-3 h-3" />
                                    {task.subtasks.filter((s) => s.completed).length}/{task.subtasks.length}
                                  </span>
                                )}
                              </div>
                            </div>
                          </div>
                        </AnimatedCard>
                      ))}
                      <Button variant="ghost" size="sm" className="w-full h-8 text-muted-foreground hover:text-foreground text-xs gap-2">
                        <Plus className="h-3 w-3" /> Add Task
                      </Button>
                    </div>
                  </div>
                )
              })}
            </div>
          )}

          {view === "list" && (
            <AnimatedCard className="overflow-hidden animate-in fade-in slide-in-from-bottom-4 duration-500">
              <div className="overflow-x-auto">
                <table className="w-full text-sm text-left">
                  <thead className="text-xs text-muted-foreground bg-muted/50 uppercase">
                    <tr>
                      <th className="px-4 py-3 font-medium">Task Name</th>
                      <th className="px-4 py-3 font-medium">Status</th>
                      <th className="px-4 py-3 font-medium">Priority</th>
                      <th className="px-4 py-3 font-medium">Assignee</th>
                      <th className="px-4 py-3 font-medium text-right">Due Date</th>
                    </tr>
                  </thead>
                  <tbody className="divide-y divide-border">
                    {filteredTasks.map((task) => (
                      <tr
                        key={task.id}
                        className="hover:bg-muted/30 cursor-pointer transition-colors"
                        onClick={() => {
                          setSelectedTask(task)
                          setIsTaskDetailsOpen(true)
                        }}
                      >
                        <td className="px-4 py-3">
                          <div className="flex items-center gap-3">
                            <Checkbox
                              checked={task.status === "completed"}
                              onClick={(e) => {
                                e.stopPropagation()
                                toggleTaskStatus(task.id)
                              }}
                            />
                            <div>
                              <p className={cn("font-medium", task.status === "completed" && "line-through text-muted-foreground")}>
                                {task.title}
                              </p>
                              <p className="text-xs text-muted-foreground">{task.project}</p>
                            </div>
                          </div>
                        </td>
                        <td className="px-4 py-3">
                          <Badge
                            variant="secondary"
                            className={cn(
                              "text-[10px] font-normal",
                              task.status === "completed" && "bg-success/10 text-success",
                              task.status === "in-progress" && "bg-primary/10 text-primary",
                            )}
                          >
                            {statusConfig[task.status].label}
                          </Badge>
                        </td>
                        <td className="px-4 py-3">
                          <Badge className={cn("text-[10px] font-normal border-0", priorityConfig[task.priority].color)}>
                            {priorityConfig[task.priority].label}
                          </Badge>
                        </td>
                        <td className="px-4 py-3">
                          <div className="flex items-center gap-2">
                            <Avatar className="w-6 h-6">
                              <AvatarFallback className="text-[10px] bg-primary/20 text-primary">
                                {task.assignee.initials}
                              </AvatarFallback>
                            </Avatar>
                            <span className="text-xs">{task.assignee.name}</span>
                          </div>
                        </td>
                        <td className="px-4 py-3 text-right text-xs text-muted-foreground">
                          {formatDate(task.dueDate)}
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </AnimatedCard>
          )}

          {view === "calendar" && (
            <AnimatedCard className="p-8 flex items-center justify-center text-muted-foreground animate-in zoom-in-95 duration-500">
              <div className="text-center">
                <CalendarIcon className="w-12 h-12 mx-auto mb-4 opacity-20" />
                <h3 className="text-lg font-medium">Calendar View Coming Soon</h3>
                <p className="text-sm">We are working on bringing a full interactive calendar to your tasks.</p>
              </div>
            </AnimatedCard>
          )}

          {view === "timeline" && (
            <AnimatedCard className="p-8 flex items-center justify-center text-muted-foreground animate-in zoom-in-95 duration-500">
              <div className="text-center">
                <History className="w-12 h-12 mx-auto mb-4 opacity-20" />
                <h3 className="text-lg font-medium">Gantt/Timeline View Coming Soon</h3>
                <p className="text-sm">Plan long-term projects with our upcoming timeline view.</p>
              </div>
            </AnimatedCard>
          )}
        </div>

        {/* Task Detail Dialog */}
        <Dialog open={!!selectedTask} onOpenChange={() => setSelectedTask(null)}>
          <DialogContent className="sm:max-w-[500px]">
            {selectedTask && (
              <>
                <DialogHeader>
                  <div className="flex items-center gap-2 mb-2">
                    <Badge className={`${priorityConfig[selectedTask.priority].color} border-0`}>
                      <Flag className="w-3 h-3 mr-1" />
                      {priorityConfig[selectedTask.priority].label}
                    </Badge>
                    <Badge
                      className={`${statusConfig[selectedTask.status].color.replace("text-", "bg-").replace("-foreground", "/20")} border-0`}
                    >
                      {statusConfig[selectedTask.status].label}
                    </Badge>
                  </div>
                  <DialogTitle>{selectedTask.title}</DialogTitle>
                  <p className="text-muted-foreground text-sm">{selectedTask.project}</p>
                </DialogHeader>

                <div className="space-y-4 mt-4">
                  <div>
                    <p className="text-xs text-muted-foreground mb-1">Description</p>
                    <p className="text-sm bg-secondary/50 p-3 rounded-lg">{selectedTask.description}</p>
                  </div>

                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <p className="text-xs text-muted-foreground mb-1">Assignee</p>
                      <div className="flex items-center gap-2">
                        <Avatar className="w-6 h-6">
                          <AvatarFallback className="bg-primary/20 text-primary text-xs">
                            {selectedTask.assignee.initials}
                          </AvatarFallback>
                        </Avatar>
                        <span className="text-sm">{selectedTask.assignee.name}</span>
                      </div>
                    </div>
                    <div>
                      <p className="text-xs text-muted-foreground mb-1">Due Date</p>
                      <div className="flex items-center gap-2 text-sm">
                        <Calendar className="w-4 h-4 text-muted-foreground" />
                        {new Date(selectedTask.dueDate).toLocaleDateString()}
                      </div>
                    </div>
                  </div>

                  <div>
                    <p className="text-xs text-muted-foreground mb-2">Tags</p>
                    <div className="flex flex-wrap gap-2">
                      {selectedTask.tags.map((tag) => (
                        <Badge key={tag} variant="outline">
                          <Tag className="w-3 h-3 mr-1" />
                          {tag}
                        </Badge>
                      ))}
                    </div>
                  </div>

                  {selectedTask.subtasks && selectedTask.subtasks.length > 0 && (
                    <div>
                      <p className="text-xs text-muted-foreground mb-2">Subtasks</p>
                      <div className="space-y-2">
                        {selectedTask.subtasks.map((subtask) => (
                          <div key={subtask.id} className="flex items-center gap-2 p-2 rounded-lg bg-secondary/50">
                            <Checkbox checked={subtask.completed} />
                            <span className={cn("text-sm", subtask.completed && "line-through text-muted-foreground")}>
                              {subtask.title}
                            </span>
                          </div>
                        ))}
                      </div>
                    </div>
                  )}
                </div>
              </>
            )}
          </DialogContent>
        </Dialog>
      </div>
    </DashboardLayout>
  )
}
